<template><section><h1>steps 步骤条组件</h1>
<blockquote>
<p>基础用法</p>
</blockquote>
<demo-block>
                  <div class="source" slot="source"><template>
    <div>
      <div><steps :items="items" :activeIndex="index"></steps></div>
      <br>
      <div><button-component @click.native="next">下一步</button-component></div>
    </div>
</template>

</div>
                  <h3>通过 进度 条加加的 方式实现</h3>

                  <div class="highlight" slot="highlight"><pre v-pre><code v-pre class="language-html"><span class="hljs-tag">&lt;<span class="hljs-name">template</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">steps</span> <span class="hljs-attr">:items</span>=<span class="hljs-string">"items"</span> <span class="hljs-attr">:activeIndex</span>=<span class="hljs-string">'index'</span>/&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">br</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span><span class="hljs-tag">&lt;<span class="hljs-name">button-component</span> <span class="hljs-attr">@click.native</span>=<span class="hljs-string">"next"</span>&gt;</span>下一步<span class="hljs-tag">&lt;/<span class="hljs-name">button-component</span>&gt;</span><span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">import</span> { Steps } <span class="hljs-keyword">from</span> <span class="hljs-string">'vo-ui'</span>
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
	<span class="hljs-attr">components</span>: {
        Steps
    },
	data() {
		<span class="hljs-keyword">return</span> {
			<span class="hljs-attr">items</span>: [{<span class="hljs-attr">num</span>: <span class="hljs-string">'一'</span>,<span class="hljs-attr">text</span>: <span class="hljs-string">'测试阶段1'</span>},{<span class="hljs-attr">num</span>: <span class="hljs-string">'二'</span>,<span class="hljs-attr">text</span>: <span class="hljs-string">'测试阶段2'</span>},{<span class="hljs-attr">num</span>: <span class="hljs-string">'三'</span>,<span class="hljs-attr">text</span>: <span class="hljs-string">'测试阶段3'</span>}],
			<span class="hljs-attr">index</span>: <span class="hljs-number">0</span>
		}
	},
	<span class="hljs-attr">methods</span>: {
		next () {
			<span class="hljs-keyword">if</span> (<span class="hljs-keyword">this</span>.index &lt; <span class="hljs-number">2</span>) {
				<span class="hljs-keyword">this</span>.index += <span class="hljs-number">1</span>
			} <span class="hljs-keyword">else</span> {
				<span class="hljs-keyword">this</span>.index = <span class="hljs-number">0</span>
			}
		},
	}
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<blockquote>
<h2>开关配置</h2>
</blockquote>
<table>
<thead>
<tr>
<th>名称</th>
<th style="text-align:left">说明</th>
</tr>
</thead>
<tbody>
<tr>
<td>:items=&quot;items&quot;</td>
<td style="text-align:left">步骤条数据</td>
</tr>
<tr>
<td>:activeIndex</td>
<td style="text-align:left">当前在第几项</td>
</tr>
</tbody>
</table>
</section></template>

<script>
import { Steps } from 'vo-ui'
export default {
	components: {
        Steps
    },
	data() {
		return {
			items: [{num: '一',text: '测试阶段1'},{num: '二',text: '测试阶段2'},{num: '三',text: '测试阶段3'}],
			index: 0
		}
	},
	methods: {
		next () {
			if (this.index < 2) {
				this.index += 1
			} else {
				this.index = 0
			}
		},
	}
}
</script>


